import * as React from "react";
import './PlayerPage.css'
import {NflTeam} from "../services/NflTeamService";
import {Roster} from "./Roster";
import _ from "lodash";
import {Player} from "../services/RosterService";
import {Team} from "../services/TeamService";
import {getUrl} from "../services/urlService";
// import Timeout = NodeJS.Timeout;

export interface PlayerPageProps {
  nflTeams: Array<NflTeam>;
  players: Array<Player>;
}
export class PlayerPage extends React.Component<PlayerPageProps> {

  state:PlayerPageProps;
  // timer:Timeout;
  constructor(pro:PlayerPageProps) {
    super(pro);
    this.state = {nflTeams:[],players:[]};
    this.getPlayers = this.getPlayers.bind(this)
    // setInterval(()=> this.getPlayers(), 1000);
  }
  getPlayers(){
    console.log("run")
    fetch(getUrl() +"/player")
    .then(response => response.json())
    .then(data =>{
      console.log("got players")
      this.setState({ players: data, isLoading: true, nflTeams: this.state.nflTeams })
      return fetch(getUrl() +"/static/nflTeams")
      .then(response => response.json())
      .then(data =>{
        console.log("got time")
        this.setState({nflTeams: data, isLoading: false, players: this.state.players});
      });
    } );
    // .then(data =>{
    //   console.log(data)
    //   this.setState({...this.state,players:data, nflTeams:this.state.nflTeams});
    //   this.forceUpdate();
    // } )
  }

  componentDidMount() {
    this.setState({nflTeams: [], isLoading: true,players: []});
    // this.state: IPlayerState{isLoading: true}
    fetch(getUrl() +"/player")
    .then(response => response.json())
    .then(data =>{
        this.setState({ players: data, isLoading: true, nflTeams: this.state.nflTeams })
        return fetch(getUrl() +"/static/nflTeams")
        .then(response => response.json())
        .then(data => this.setState({nflTeams: data, isLoading: false, players: this.state.players}));
    } )

  }


  render() {
    return (
      <div className="container">







        <div className="row">
          {_.filter(this.state.nflTeams,
            (data:NflTeam)=> {return data.conf === 'AFC'}).map(
              (value:NflTeam, index:number) => {
            return(
              <div className="col">
                   <div className="row ">
                     {/*<img src={logo}/>*/}
                     {/*<img src="%PUBLIC_URL%/NE.png"/>*/}
                     {/*<img src={process.env.PUBLIC_URL + value.logo}/>*/}
                     <h3 className="col-12">{value.name}</h3>
                   </div>
              <div className="row">
                <Roster key={value.name} players={_.filter(this.state.players, (data: Readonly<Player>) =>{
                  return data.nflTeam.name === value.name;
                }) }/>
              </div>
              </div>)})}
        </div>

        <div className="row">
          {_.filter(this.state.nflTeams,(data:NflTeam)=> {return data.conf === 'NFC'}).map((value:NflTeam, index:number) => {
            return(
              <div className="col">
                <div className="row justify-content-center">
                  {/*<img src={logo}/>*/}
                  {/*<img src="%PUBLIC_URL%/NE.png"/>*/}
                  {/*<img src={process.env.PUBLIC_URL + value.logo}/>*/}
                  <h3>{value.name}</h3>
                </div>
                <div className="">
                  <Roster key={value.name} players={_.filter(this.state.players, (data: Readonly<Player>) =>{
                    return data.nflTeam.name === value.name;
                  }) }/>
                </div>
              </div>)})}
        </div>
      </div>
    );

  }
}
