import * as React from "react";
import {Players} from "../public/Players";
import {Player} from "../services/RosterService";
import "./Roster.css"
interface RosterProps {
  players: Array<Player>;
}
export class Roster extends React.Component<RosterProps> {

  // readonly state: {x:Player[]};

  constructor(a:RosterProps) {
    super(a);
    // this.state = {x:Array<Player>()};
    ["QB","RB","WR","TE","K","DST"].map((value:string)=>{
      a.players.map((player:Player)=>{
        if(player.position === value){
          this.props.players.push(player)
        }
      })
    })

  }

  componentDidMount(): void {
    this.setState({data:[]})
  }

  getClazz(p:Player ):string {
    if(p.team2 != undefined){
      return "row text-left text-muted"
    }
    else if(p.team1 != undefined){
      return "row text-left text-warning"
    }
    return "row text-left text-primary";
  }

  render(){
    return (
      <div className="">
      {this.props.players.map((value:Player,index:number) => {
          return (
            <div className={this.getClazz(value)}>
              <div className="col-2">{value.position}</div>
              <div className="col-8">
                {value.name}
              </div>
            </div>
          )})}

      </div>);
  }
}